import axios from "axios";
import { endpoints } from "Constants/api";
import axiosHelpers from "Constants/axiosHelpers";
import {
  updateResidentTypes,
  updatePropertyTypes,
  setBasicDetails
} from "../redux/slice/basicSlice";



export const addCompanyDetails = async (data) => {
  let request = await axios({
    method: "post",
    url: `${endpoints?.companyDetails.ADD_COMPANY}`,
    headers: {
      // Authorization: `Bearer ${localStorage.getItem("token")}`,
    },
    data,
  }).catch(axiosHelpers);
  return request;
};

export const getCompanyDetails = async (propertyId, createdBy) => {
  try {
    const url = `${endpoints.companyDetails.GET_COMPANY_DETAILS}/${propertyId}/${createdBy}`;
    const response = await axios.get(url, {
      headers: {
        // Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });
    return response.data;
  } catch (error) {
    console.error("Error fetching company details:", error);
    throw error;
  }
};
